body {
  background-color: #151515;
  color: #eee;
}
.App {
  font-family: sans-serif;
  text-align: center;
}

.input-area {
  height: 300px;
  width: 100%;
  background-color: #353535;
  border: 1px solid #3c3a3a;
  color: #eee;
}

table.table-cmd {
  font-size: 0.8em;
  background: #303030;
  color: #eee;
}
table.table-cmd td {
  padding: 3px 15px;
  margin: 0;
  border: none;
}

table.table-cmd tr {
  background: #484848;
}
table.table-cmd th {
  background: #484848;
}

.table-cmd tr:nth-child(even) {
  background-color: #333;
  color: #eee;
}
.table-cmd td:nth-child(1) {
  font-weight: bold;
  text-align: left;
}
.table-cmd td:nth-child(2) {
  text-align: left;
}

.btn-parse {
  max-width: 200px;
  float: right;
}

.nav.sidebar .nav-link {
  color: #fff;
  border-bottom: 1px solid #fff;
  border-radius: 0;
}
.nav.sidebar .nav-link:hover {
  color: #007bff;
  border-bottom: 1px solid #007bff;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-align: left;
}
hr {
  border-bottom: 1px solid #fff;
}
div.form-group label {
  text-align: left;
  width: 100%;
}
div.result-container {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 10px;
}
div.result-container > div.result {
  text-align: left;
  background: #333;
  padding: 10px;
  border-radius: 3px;
  word-wrap: break-word;
  width: 80%;
}
div.result-container > button {
  flex: 1;
  margin-left: 10px;
}
input.hidden-field {
  position: absolute;
  top: -9999px;
  left: -99999px;
}

.ip-input {
  max-width: 300px;
  display: flex;
  justify-content: space-between;
}
.ip-input input[type="text"] {
  display: inline-block;
  width: 60px;
}
.ip-input span {
  width: 20px;
  font-weight: bold;
  display: block;
  align-self: flex-end;
}

.encrypt-area {
  display: flex;
  justify-content: flex-end;
}
.encrypt-area button + button {
  margin-left: 10px;
}

.nav-link.action {
  cursor: pointer;
}

.forgot-area {
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin: 0 auto;
  max-width: 1000px;
  text-align: center;
}
.forgot-area img {
  width: 300px;
  margin: 0 auto;
}

