html,
body {
  height: 100%;
}
.login-container {
  display: flex;
  width: 350px;
  height: 300px;
  background-color: #353535;
  border-radius: 4px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -175px;
  margin-top: -150px;
}

.login-box {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.logo {
  height: 150px;
}
.logo h3 {
  text-align: center;
  margin-top: 30px;
}
.input-area {
  display: flex;
  flex-direction: column;
  padding: 10px 25px;
}

.link-forgot-pwd {
  text-align: center;
  font-size: 0.89em;
}

p.login-error {
  color: #980000;
  background: #ef9b9b;
  padding: 3px 15px;
  border-radius: 3px;
  margin-top: 3px;
  font-size: 0.89em;
  text-align: center;
}
