.header-menu {
    display: flex;
    justify-content: flex-end;
}

.header-user-info {
    display: flex;
    height: 60px;
    padding: 15px 10px;
}
.header-user-info img {
    height: 25px;
    width: 25px;
    margin-right: 10px;
    border-radius: 15px;
}
.btn-logout, .btn-logout:hover {
    color: #fff;
    font-size: 16px;
    margin: 0;
    padding: 0px 10px;
    margin-top: 12px;
    height: 30px;
    font-size: 12px;
    background: #363636;
}